@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
}
a {
  text-decoration: none;
}
#nav-bar {
  width: 100%;
  height: 80px;
  background-color: white;
  box-shadow: 0 1px 1px #ccc;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 78px;
}
#left-sec {
  display: flex;
}
#left-sec a {
  margin-right: 70px;
}
#right-sec {
  display: flex;
  align-items: center;
}
#logo-edy {
  color: #0084ff;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.08;
  letter-spacing: -0.9px;
  text-align: left;
  text-transform: uppercase;
  font-size: 32px;
}
#logo-sto {
  font-weight: 500;
  font-style: italic;
  font-stretch: normal;
  line-height: 0.8;
  letter-spacing: 0.9px;
  color: #000;
}
#explore {
  display: flex;
  align-items: center;
}
#explore p {
  margin-right: 8px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: 0.9px;
  text-align: center;
  cursor: pointer;
  font-size: 14px;
}
#des {
  margin-right: 60px;
}
#des p {
  color: #999797;
  font-size: 15.5px;
  line-height: 1;
}
#btn {
  border: none;
  outline: none;
  padding: 8px 26px;
  height: 42px;
  color: white;
  background: linear-gradient(181deg, #0048ff, #0096ff);
  border-radius: 25px;
  font-weight: 600;
  font-size: 12px;
}
#filter {
  margin-top: 100px;
  padding: 20px 180px;
}
#filter-wrapper {
  display: flex;
  align-items: center;
  height: 20px;
  margin-bottom: 20px;
}
#filter-wrapper img {
  margin-right: 5px;
  height: 100%;
}
#filter-wrapper p {
  font-size: 14px;
  font-weight: 600;
}
#filters {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.filter-items {
  margin-right: 20px;
  border: 1px solid #0084ff;
  margin-bottom: 10px;
  padding: 6px 16px;
  border-radius: 15px;
  color: #0084ff;
  font-size: 12px;
}
#cards-wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 20px 180px;
  width: 100%;
}
.card {
  width: 30%;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 5px;
  border: 1px solid rgba(0, 168, 255, 0.3);
  overflow: hidden;
}
.image-warpper {
  width: 100%;
  height: 45%;
}
.image {
  width: 100%;
  height: 100%;
}
.text-wrapper {
  width: 100%;
  height: 55%;
  padding: 12px 22px;
}
.text-wrapper h1 {
  font-size: 20px;
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 10px;
}
.text-wrapper h3 {
  font-size: 14px;
  font-weight: 500;
  line-height: 2;
  color: #0096ff;
  padding-top: 4px;
  margin-bottom: 10px;
}
.text-wrapper h3 span {
  color: #000;
}
.text-wrapper p {
  color: #999797;
  white-space: pre-wrap;
  margin-bottom: 80px;
}
